import React from 'react'
import Flex from '../StyledComponents/Flex'
import Button from '../StyledComponents/Button'
import { useDispatch, useSelector } from 'react-redux'
import {
    addAsyncTodo,
    addTodo,
    decrement,
    increment,
    removeLastTodo,
} from '../../toolKitRedux/toolkitSlice'

const Homepage = () => {
    const dispatch = useDispatch()
    const count = useSelector((state) => state.toolkit.count)
    const todos = useSelector((state) => state.toolkit.todos)

    return (
        <Flex>
            <div>{count}</div>
            <Button onClick={() => dispatch(increment())}>–ò–Ω–∫—Ä–µ–º–µ–Ω—Ç</Button>
            <Button onClick={() => dispatch(decrement())}>–î–µ–∫—Ä–µ–º–µ–Ω—Ç</Button>
            <Button onClick={() => dispatch(addTodo(prompt()))}>
                –î–æ–±–∞–≤–∏—Ç—å —Ç—É–¥—É
            </Button>
            <Button onClick={() => dispatch(removeLastTodo())}>
                –£–¥–∞–ª–∏—Ç—å –ø–æ—Å–ª–µ–¥–Ω–∏–π —Ç—É–¥—É
            </Button>
            <Button onClick={() => dispatch(addAsyncTodo())}>
                –î–æ–±–∞–≤–∏—Ç—å –∞—Å–∏–Ω–∫ —Ç—É–¥—É
            </Button>
            {todos.map((todo) => (
                <div>{todo}</div>
            ))}
        </Flex>
    )
}

export default Homepage
